@import url("https://fonts.googleapis.com/css?family=Josefin+Sans");
@import url("https://fonts.googleapis.com/css?family=Poppins");
@import url("https://fonts.googleapis.com/css?family=Crimson+Text");

@font-face {
	font-family: "in-your-face-joffrey";
	src: url("../fonts/In-your-face-joffrey.ttf");
}

html,
body {
	scroll-behavior: smooth;
}

body {
	background: url("../Images/bg2.png") no-repeat;
	background-size: 100%;
}

/* --------------- Start Home Page ------------------- */
#homeTitle {
	font-size: 100px;
	font-family: "Josefin Sans";
	letter-spacing: 8px;
	text-transform: uppercase;
	/* color: #d9d9d9; */
	background: -webkit-linear-gradient(#75c7df, #aab418);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}

.progress {
	transform: rotate(-90deg);
}

#homePageMain {
	background: url("../Images/bg2.png") no-repeat;
	background-size: cover;
	/* background: linear-gradient(to right, #f7f701, #870086); */
	/* color: white; */
	color: #d9d9d9;
	/* width: 50%; */
	/* background-color: #a2dbeb; */
}

#homePage {
	background: linear-gradient(to bottom, #5603ad, #007cbe);
	text-align: center;
	padding-left: 10%;
	padding-right: 10%;
	width: 90%;
	margin: 0 auto;
	width: 60%;
}

.homePageText {
	font-size: 40px;
	font-family: "poppins";
	margin-left: -80px;
}

#joinText {
	font-size: 40px;
	font-family: "poppins";
	margin-left: 45px;
}

.nameInput {
	font-family: "poppins";
	margin-left: 20px;
}

.nameForm {
	position: relative;
	width: 95%;
}

#Loading {
	position: absolute;
	left: 50%;
	top: 50%;
	-webkit-transform: translate(-50%, -50%);
	transform: translate(-50%, -50%);
}

/* --------------- End Home Page ------------------- */

/* --------------- Start Question ------------------- */
#questionDiv {
	/* background-color: #a2dbeb; */
	/* background-color: black; */

	height: 100vh;
}

#questionCard {
	/* background: #75c7df;	 */
	right: 0%;
	border-radius: 10px 0 0 10px;
	border-right: none;
	/* background: linear-gradient(to bottom, #F78636, #F02A68); */
	background-color: #bc82eb;
}

.question {
	display: inline-block;
	color: white;
	font-size: 35px;
	font-family: "Poppins";
	position: relative;
	margin-top: 75px;
	font-weight: bold;
	margin-right: 30%;
	/* border-right: thick solid #ff0000; */
}
/* --------------- End Question ------------------- */
#questionCard,
#choicesCard {
	height: 80%;
	width: 80%;
	transform: translateY(-50%);
	top: 50%;
	position: absolute;
}

#submitButton {
	margin-top: 20px;
	/* margin-left: 300px; */
}
/* --------------- Start Choices ------------------- */

#choicesDiv {
	/* background-color: #9acfdf; */
	height: 100vh;
	/* background-color: black; */
	/* background: linear-gradient(to top, #5603ad, #007cbe); */
}

#choicesCard {
	background-color: #17bebb;
	left: 0%;
	border-radius: 0 10px 10px 0px;
	border-left: none;
}

#choicesCard .card-body {
	margin: 90px 0 auto 45%;
}

.numberCircle {
	border-radius: 50%;
	width: 35px;
	height: 35px;
	padding-top: 8px;
	/* background: #79b6c7; */
	/* box-shadow: 4px 4px 10px #79b6c7; */
	color: white;
	text-align: center;
	font: 20px sans-serif;
}

.choiceName {
	margin-top: 3px;
	margin-left: 20px;
	font-weight: bold;
	font-size: 20px;
}

.choiceButton {
	background: none;
	color: inherit;
	border: none;
	padding: 0;
	font: inherit;
	cursor: pointer;
}

.choiceButton:focus {
	outline: none;
}
/* --------------- End Choices ------------------- */

#brain {
	position: absolute;
	z-index: 2;
	top: 20%;
	left: 35%;
}

.btn-primary {
	background-color: #f433ab !important;
	/* background-color: rgba(224, 51, 171) !important; */
	border-color: #f433ab !important;
	/* color: rgba(255, 255, 255, 1) !important; */
}

.btn-primary:hover {
	background: transparent !important;
	color: black !important;
}

/* --------------- Start Leaderboard ------------------- */
#boardCard {
	width: 1100px;
	margin: 0 auto;
	margin-top: 100px;
	background-color: #e9ecef;
}

.tableStyle {
	/* width: 500px; */
	text-align: center;
	border-collapse: collapse;
	margin: 25px 50px;
	font-size: 0.9em;
	font-weight: bold;
	box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
}

.tableStyle thead tr {
	background-color: #9d50db;
	font-size: 17px;
	color: white;
	text-align: center;
}

.tableStyle th,
.tableStyle td {
	padding: 12px 15px;
}

.tableStyle tbody tr {
	border-bottom: 1px solid #dddddd;
	background-color: white;
}

.tableStyle tbody tr:nth-of-type(2n) {
	background-color: #f3f3f3;
}

.tableStyle td:nth-child(2),
th:nth-child(2) {
	width: 4%;
}

.tableStyle td:nth-child(2) {
	text-align: left;
}

.tableStyle tbody tr:last-of-type {
	border-bottom: 2px solid #9d50db;
}

.tableStyle tbody tr:hover {
	font-weight: bold;
	color: white;
	background-color: #9d50db;
}

.table-container table {
	margin-left: auto;
	margin-right: auto;
}

.tableStyle tbody tr:first-child {
	background-color: #252e38;

	color: white;
}

/* --------------- End Leaderboard ------------------- */

#wrongCard {
	background-color: red;
	overflow-y: scroll;
	height: 300px;
}
